
* {
  margin: 0;
  padding: 0;
}
#app {
  margin: 50px auto;
}
.layout {
  width: 900px;
  margin: 0 auto;
}
#nav {
  position: fixed;
  height: 40px;
  background-color: #000;
  opacity: 0.7;
  top: 0;
  left: 0;
  right: 0;
}
#nav span {
    color: #fff;
}
#nav .btn {
    line-height: 40px;
    color: #fff;
    margin-right: 20px;
    cursor: pointer;
}
#dialogWapper {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.3);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#dialogWapper #myDialog {
    width: 300px;
    height: 200px;
    background: #fff;
}
#todo #header {
  margin: 10px auto;
  border-bottom: 2px solid green;
}
#todo #todos li {
  height: 40px;
  margin-top: 20px;
  border-bottom: 1px solid;
  line-height: 40px;
}
#todo #todos li:after {
    content: '';
    display: block;
    clear: both;
}
#todo #todos li .todo-title {
    display: inline-block;
    width: 400px;
}
#todo #todos li .created-time {
    font-size: 12px;
    color: #333;
}
#todo #todos li .done-or-not {
    display: inline-block;
    width: 100px;
    color: #238;
}
#todo #todos li .remove {
    float: right;
    width: 60px;
    height: 30px;
    border-radius: 10px;
    background-color: orange;
    color: #fff;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
}



